<template>
<div id="header">
	<div class="logo">
		<img src="../assets/logo.png">
	</div>
	<ul class="nav">
		<router-link to="" tag="li">Cluster Map Info</router-link>
		<router-link to="" tag="li">Binary编译时间</router-link>
		<router-link to="" tag="li">全量数据版本</router-link>
		<router-link to="" tag="li">协议/接口版本</router-link>
		<router-link to="" tag="li">查询节点</router-link>
		<router-link to="" tag="li" v-if="isLogin && isAdmin">模块管理</router-link>
		<router-link to="" tag="li" v-if="isLogin && isAdmin">权限管理</router-link>
		<router-link to="" tag="li" v-if="isLogin && isAdmin">ClusterMap配置</router-link>
	</ul>
	<div class="user">
		<span>Log in</span>
		<span v-if="isLogin">{{ userName }}</span>
		<span v-if="isLogin">Log out</span>
	</div>
</div>
</template>

<script>
export default {
	data (){
		return {
			isLogin: false,	// 是否登录
			isAdmin: false, // 是否管理员权限
			userName: ''    // 用户名
		}
	},
	created (){
		this.checkLogin();
		if (this.isLogin){
			this.checkAdmin();
		}
	},
	watch: {
		$route: 'checkLogin'
	},
	methods: {
		checkLogin (){
			this._Cookie.set();
		},
		checkAdmin (){

		}
	}
}
	
</script>

<style scoped>
#header {
	height: 56px;
	line-height: 56px;
	/*background: #666;*/
	/*color: #f1f2f3;*/
}
#header .logo {
	float: left;
}
#header .nav {
	float: left;
}
#header .nav li {
	float: left;
	margin: 0 12px;
}
#header .user {
	float: right;
}
</style>


